<template>
  <div class="edit">
    <v-container
      fluid
      grid-list-xl>

      <!-- 錯誤訊息 -->
      <CommonErrorMsg
        :show.sync="error.show"
        :message.sync="error.message"
      />

      <!-- 修改中 mask -->
      <MaterialLoadingMask
        v-if="modifyingStatus"
        :showText="$ln('修改中')"
      />

      <!-- 標題 & 返回按鈕 -->
      <v-layout
        row
        wrap>
        <v-flex
          md6
          tag="h3"
          class="mt-0">
          {{ $ln('修改會員') }} ({{ Items.account }})
        </v-flex>
        <v-flex
          md6
          class="text-xs-right">
          <v-btn
            v-if="!loadingStatus"
            small
            color="amber darken-3"
            @click="toBack()">
            <v-icon>mdi-arrow-left</v-icon>
            {{ $ln('返回') }}
          </v-btn>
          <v-btn
            v-else
            disabled
            small
            color="amber darken-3">
            <v-icon>mdi-arrow-left</v-icon>
            {{ $ln('返回') }}
          </v-btn>
        </v-flex>
      </v-layout>

      <!-- 按鈕列 -->
      <v-layout
        row
        wrap>
        <v-flex
          md6>
          <!-- 刪除帳號 -->
          <AccountManagementDeleteAccount
            :account="Items.account"
            :edit-level-code="1"
            @deleteStatus="deleteAccountStatus"
          />

          <!-- 修改額度 -->
          <AccountManagementEditQuotaMemberIndex
            :account="Items.account"
            :sport-up-limit-quota="Items.sportUpLimitQuota"
            :sport-quota="Items.sportQuota"
            :sport-surplus-quota="Items.sportSurplusQuota"
            :bingo-up-limit-quota="Items.bingoUpLimitQuota"
            :bingo-quota="Items.bingoQuota"
            :bingo-surplus-quota="Items.bingoSurplusQuota"
            :bingo-recover-win="Items.bingoRecoverWin"
            :lottery-up-limit-quota="Items.lotteryUpLimitQuota"
            :lottery-quota="Items.lotteryQuota"
            :lottery-surplus-quota="Items.lotterySurplusQuota"
            :royal-up-limit-quota="Items.royalUpLimitQuota"
            :royal-quota="Items.royalQuota"
            :royal-surplus-quota="Items.royalSurplusQuota"
            :royal-recover-win="Items.royalRecoverWin"
            :bingo-api-show="setApi.bingo.show"
            :lottery-api-show="setApi.lottery.show"
            :royal-api-show="setApi.royal.show"
            @status="editQuotaStatus"
            @quotaDialogOpen="quotaDialogOpen"
          />

          <!-- 修改密碼 -->
          <AccountManagementEditPasswordIndex
            v-if="passwordItems.show"
            :account="Items.account"
            :update-type="'others'"
            :account-type="'glead'"
          />
        </v-flex>

        <!-- 確定修改 -->
        <!-- <v-flex
          md6
          class="text-md-right">
          <v-btn
            v-if="(Items.valid && valid) && !modifyingStatus"
            small
            color="deep-orange darken-1"
            @click="editAccount()">
            {{ $ln('確定修改') }}
          </v-btn>
          <v-btn
            v-else-if="!(Items.valid && valid) || modifyingStatus"
            :loading="modifyingStatus"
            disabled
            small
            color="deep-orange darken-1">
            {{ $ln('確定修改') }}
          </v-btn>
        </v-flex> -->
        <v-flex
          md6
          class="text-md-right">
          <v-btn
            v-if="!modifyingStatus"
            small
            color="deep-orange darken-1"
            @click="editAccount()">
            {{ $ln('確定修改') }}
          </v-btn>
          <v-btn
            v-else-if="modifyingStatus"
            :loading="modifyingStatus"
            disabled
            small
            color="deep-orange darken-1">
            {{ $ln('確定修改') }}
          </v-btn>
        </v-flex>
      </v-layout>

      <!-- 修改內容 -->
      <v-form v-model="Items.valid">
        <material-card>
          <v-layout wrap>
            <v-flex
              xl9
              md8>
              <v-layout wrap>
                <!-- 修改時間 -->
                <v-flex md4>
                  <v-text-field
                    v-model="Items.lastDateTime"
                    :label="$ln('修改時間')"
                    disabled
                  />
                </v-flex>

                <!-- 登入時間 -->
                <v-flex md4>
                  <v-text-field
                    v-model="Items.loginTime"
                    :label="$ln('登入時間')"
                    disabled
                  />
                </v-flex>

                <!-- 投注時間 -->
                <v-flex md4>
                  <v-text-field
                    v-model="Items.betTime"
                    :label="$ln('投注時間')"
                    disabled
                  />
                </v-flex>

                <!-- 會員帳號 -->
                <v-flex md4>
                  <v-text-field
                    v-model="Items.account"
                    :label="$ln('會員帳號')"
                    disabled
                  />
                </v-flex>

                <!-- 會員名稱 -->
                <v-flex md4>
                  <v-text-field
                    v-model="Items.nameItems.name"
                    :rules="Items.nameItems.nameRules"
                    :label="$ln('會員名稱')"
                  />
                </v-flex>

                <!-- 單注下限 -->
                <v-flex md4 v-if="Items.downLimitItems.show">
                  <v-text-field
                    v-if="!Items.downLimitItems.disabled"
                    v-model.number="Items.downLimitItems.amount"
                    :rules="Items.downLimitItems.rules"
                    :suffix="`≥${Items.downLimitItems.minLimit}`"
                    :disabled="Items.downLimitItems.disabled"
                    :label="$ln('單注下限')"
                  />
                  <v-text-field
                    v-else
                    v-model.number="Items.downLimitItems.amount"
                    :suffix="`≥${Items.downLimitItems.minLimit}`"
                    :disabled="Items.downLimitItems.disabled"
                    :label="$ln('單注下限')"
                  />
                </v-flex>

                <!-- (體育)信用額度 -->
                <v-flex md8 v-show="Items.sportCreditItems.show">
                  <v-text-field
                    v-model.number="Items.sportCreditItems.amount"
                    :suffix="`${Items.sportCreditItems.suffixLeft}${Items.sportCreditItems.todayDate}: ${amountFormat(Items.sportTodayQuota)}${Items.sportCreditItems.suffixRight} / ${Items.sportCreditItems.tomorrowDate}: ${amountFormat(Items.sportTomorrowQuota)}${Items.sportCreditItems.suffixRight}`"
                    :disabled="Items.sportCreditItems.disabled"
                    :label="$ln('(體育)信用額度')"
                    class="v-input-input"
                  />
                </v-flex>

                <!-- (體育)現金額度 -->
                <v-flex md8 v-show="Items.sportCashItems.show">
                  <v-text-field
                    v-model.number="Items.sportCashItems.amount"
                    :suffix="`${Items.sportCashItems.suffixRight}`"
                    :disabled="Items.sportCashItems.disabled"
                    :label="$ln('(體育)現金額度')"
                  />
                </v-flex>

                <!-- 備註 -->
                <v-flex md4>
                  <v-text-field
                    v-model="Items.remark.contents"
                    :label="$ln('備註')"
                  />
                </v-flex>
              </v-layout>
            </v-flex>

            <v-flex
              xl3
              md4
              class="pl-4">
              <!-- API設定 -->
              <div class="py-3" v-show="!(!setApi.bingo.show && !setApi.lottery.show && !setApi.royal.show)">
                <div class="v-input__slot">
                  <div class="v-text-field__slot">
                    <label
                      aria-hidden="true"
                      class="v-label caption"
                      style="left: 0px; right: auto; position: absolute;">{{ $ln('API設定') }}</label>
                    <div class="pt-3">
                      <AccountManagementEditBingoMemberIndex
                        class="mb-1"
                        v-show="setApi.bingo.show"
                        :bingo-account="Items.account"
                        :bingo-quota="Items.bingoQuota"
                        :bingo-surplus-quota="Items.bingoSurplusQuota"
                        :bingo-recover-win="Items.bingoRecoverWin"
                        :up-status="upStatus.bingo"
                        :up-close="upClose.bingo"
                        :loading-status="setApi.bingo.loadingStatus"
                        :edit-level-code="1"
                        @editBingoStatus="editBingoStatus"
                        @bingoDialogOpen="bingoDialogOpen"
                      />
                      <AccountManagementEditLotteryIndex
                        class="mb-1"
                        v-show="setApi.lottery.show"
                        :lottery-account="Items.account"
                        :lottery-quota="Items.lotteryQuota"
                        :lottery-surplus-quota="Items.lotterySurplusQuota"
                        :up-status="upStatus.lottery"
                        :up-close="upClose.lottery"
                        :up-account="getUpAccountValue()"
                        :loading-status="setApi.lottery.loadingStatus"
                        :edit-level-code="1"
                        @editLotteryStatus="editLotteryStatus"
                        @lotteryDialogOpen="lotteryDialogOpen"
                      />
                      <AccountManagementEditRoyalMemberIndex
                        class="mb-1"
                        v-show="setApi.royal.show"
                        :royal-account="Items.account"
                        :royal-quota="Items.royalQuota"
                        :royal-surplus-quota="Items.royalSurplusQuota"
                        :royal-up-limit-quota="Items.royalUpLimitQuota"
                        :royal-recover-win="Items.royalRecoverWin"
                        :royal-up-limit-level="Items.royalUpLimitLevel"
                        :up-status="upStatus.royal"
                        :loading-status="setApi.royal.loadingStatus"
                        :promptGreaterUpGameSetDialogOpen.sync="promptGreaterUpGameSetDialogOpen"
                        :promptLesserDownGameSetDialogOpen.sync="promptLesserDownGameSetDialogOpen"
                        :edit-level-code="1"
                        @editRoyalStatus="editRoyalStatus"
                        @royalDialogOpen="royalDialogOpen"
                      />
                    </div>
                  </div>
                </div>
              </div>

              <v-layout wrap>
                <!-- 是否允許登入 -->
                <v-flex
                  sx6>
                  <div class="v-input__slot">
                    <div class="v-text-field__slot">
                      <label
                        aria-hidden="true"
                        class="v-label caption"
                        style="left: 0px; right: auto; position: absolute;">{{ $ln('是否允許登入') }}</label>
                      <v-radio-group
                        v-model="Items.whetherAllowLogin.selected"
                        :disabled="upStatus.sport !== 0"
                        row>
                        <v-radio
                          class="v-input-checkbox"
                          v-for="(item, i) in Items.whetherAllowLogin.options"
                          v-if="item.value === 0"
                          :key="i"
                          :label="$ln(item.text)"
                          :value="item.value"/>
                        <v-radio
                          class="v-input-checkbox"
                          v-for="(item, i) in Items.whetherAllowLogin.options"
                          v-if="item.value === 1"
                          :key="i"
                          :label="$ln(item.text)"
                          :value="item.value"
                          @click.native.stop="allowLoginIsNoItems.dialogOpen = true"/>
                      </v-radio-group>
                    </div>
                  </div>

                  <!-- 允許登入點否提示 -->
                  <v-dialog
                    v-model="allowLoginIsNoItems.dialogOpen"
                    persistent
                    max-width="290">
                    <v-card>
                      <v-card-title class="headline">{{ $ln('不允許登入') }}</v-card-title>
                      <v-card-text>{{ $ln('是否確認「鎖定帳號」？') }}</v-card-text>
                      <v-card-actions>
                        <v-spacer/>
                        <v-btn
                          color="blue darken-1"
                          flat="flat"
                          @click="isNoAllowLogin(false)">{{ $ln('取消') }}
                        </v-btn>
                        <v-btn
                          color="blue darken-1"
                          flat="flat"
                          @click="isNoAllowLogin(true)">{{ $ln('確定') }}
                        </v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </v-flex>

                <!-- 允許投注 -->
                <v-flex
                  sx6>
                  <div class="v-input__slot">
                    <div class="v-text-field__slot">
                      <label
                        aria-hidden="true"
                        class="v-label caption"
                        style="left: 0px; right: auto; position: absolute;">{{ $ln('允許投注') }}</label>
                      <v-radio-group
                        v-model="Items.whetherAllowBet.selected"
                        :disabled="upClose.sport !== 0"
                        row>
                        <v-radio
                          class="v-input-checkbox"
                          v-for="(item, i) in Items.whetherAllowBet.options"
                          v-if="item.value === 0"
                          :key="i"
                          :label="$ln(item.text)"
                          :value="item.value"/>
                        <v-radio
                          class="v-input-checkbox"
                          v-for="(item, i) in Items.whetherAllowBet.options"
                          v-if="item.value === 1"
                          :key="i"
                          :label="$ln(item.text)"
                          :value="item.value"
                          @click.native.stop="allowBetIsNoItems.dialogOpen = true"/>
                      </v-radio-group>
                    </div>
                  </div>

                  <!-- 允許投注點否提示 -->
                  <v-dialog
                    v-model="allowBetIsNoItems.dialogOpen"
                    persistent
                    max-width="290">
                    <v-card>
                      <v-card-title class="headline">{{ $ln('不允許投注') }}</v-card-title>
                      <v-card-text>{{ $ln('是否確認「停押帳號」？') }}</v-card-text>
                      <v-card-actions>
                        <v-spacer/>
                        <v-btn
                          color="blue darken-1"
                          flat="flat"
                          @click="isNoAllowBet(false)">{{ $ln('取消') }}
                        </v-btn>
                        <v-btn
                          color="blue darken-1"
                          flat="flat"
                          @click="isNoAllowBet(true)">{{ $ln('確定') }}
                        </v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </v-flex>
              </v-layout>
            </v-flex>
          </v-layout>
        </material-card>

        <!-- 開放比賽類別 @只有會員管理有該功能@ -->
        <div class="pb-2 pt-2">
          <h4 class="title mb-2">{{ $ln('開放比賽類別') }}</h4>
          <v-layout wrap>
            <v-flex
              xl11
              md10>
              <span class="d-inline-block mr-3">
                <v-checkbox
                  class="v-input-checkbox"
                  v-model="openSportGameSelectAll"
                  :label="$ln('全部')"
                />
              </span>

              <span
                v-for="(item, i) in openSportGameItems.items"
                :key="i"
                class="d-inline-block mr-3">
                <v-layout warp row>
                  <v-flex>
                    <v-checkbox
                      class="v-input-checkbox"
                      v-model="openSportGameItems.selected"
                      :label="$ln(item.name)"
                      :value="item.id"
                      number
                    />
                  </v-flex>
                  <v-flex>
                    <span v-if="item.id === 's10'"> <!-- 足球盤 -->
                      <v-select
                        class="v-input-checkbox"
                        v-model="openSportSoccerABCItems.selected"
                        :items="openSportSoccerABCItems.options"
                        style="width: 80px;">
                        <template slot='selection' slot-scope='{ item }'>{{ $ln(item.text) }}</template>
                        <template slot='item' slot-scope='{ item }'>{{ $ln(item.text) }}</template>
                      </v-select>
                    </span>
                    <span v-else-if="item.id === 's15'"> <!-- 世足盤 -->
                      <v-select
                        class="v-input-checkbox"
                        v-model="openSportFifaABCItems.selected"
                        :items="openSportFifaABCItems.options"
                        style="width: 80px;">
                        <template slot='selection' slot-scope='{ item }'>{{ $ln(item.text) }}</template>
                        <template slot='item' slot-scope='{ item }'>{{ $ln(item.text) }}</template>
                      </v-select>
                    </span>
                  </v-flex>
                </v-layout>
              </span>
            </v-flex>
          </v-layout>
        </div>

        <!-- 說明 -->
        <v-layout wrap>
          <!-- 顯示顏色說明 -->
          <v-flex
            md4
            xs12>
            {{ $ln('顯示顏色說明') }}
            <p>
              <span class="red lighten-4">{{ $ln('紅底 - 滾球') }}</span>&nbsp;
              <span class="blue lighten-4">{{ $ln('藍底 - 過關') }}</span>&nbsp;
              <span class="amber lighten-4">{{ $ln('橘底 - 編輯狀態') }}</span>
            </p>
          </v-flex>

          <!-- 修改顏色說明 -->
          <v-flex
            md8
            xs12>
            {{ $ln('修改顏色說明') }}
            <p>
              <span class="grey lighten-3">{{ $ln('灰底 - 有修改過值') }}</span>&nbsp;
              <!-- <span class="red--text">{{ $ln('紅字 - 輸入錯誤，無法修改的值') }}</span>&nbsp;
              <span class="blue--text">{{ $ln('藍字 - 小於下線警示，但可修改的值') }}</span> -->
            </p>
          </v-flex>
        </v-layout>

        <!-- 球類設定資訊 -->
        <div class="pb-2 pt-4">
          <h4 class="title mb-2">{{ $ln('球類設定資訊') }}</h4>
          <material-card>
            <!-- 顯示修改類別 -->
            <v-layout wrap>
              <v-flex
                xl1
                md2>
                {{ $ln('顯示修改類別') }}
              </v-flex>

              <v-flex
                xl11
                md10>
                <span class="d-inline-block mr-3">
                  <v-checkbox
                    class="v-input-checkbox"
                    v-model="showGameSetSelectAll"
                    :label="$ln('全部')"
                  />
                </span>

                <span
                  v-for="(item, i) in showGameSetItems.items"
                  :key="i"
                  class="d-inline-block mr-3">
                  <v-checkbox
                    class="v-input-checkbox"
                    v-model="showGameSetItems.selected"
                    :label="$ln(item.name)"
                    :value="item.id"
                    number
                  />
                </span>
              </v-flex>
            </v-layout>

            <!-- 快速設定 -->
            <v-layout wrap>
              <v-flex
                xl1
                md2>
                {{ $ln('快速設定') }}
              </v-flex>

              <v-flex
                xl11
                md8>
                <span class="d-inline-block mr-3">
                  <v-checkbox
                    class="v-input-checkbox"
                    v-model="quickSettingGameSelectAll"
                    :disabled="disableQuickSettingGameSelectAll"
                    :label="$ln('全部')"
                  />
                </span>

                <span class="d-inline-block mr-3"
                  v-for="(item, i) in quickSettingGame.items"
                  :key="i">
                  <v-checkbox
                    class="v-input-checkbox"
                    v-model="quickSettingGame.selected"
                    :label="$ln(item.name)"
                    :value="item.id"
                    :disabled="item.disabled"
                    number
                  />
                </span>
              </v-flex>
            </v-layout>

            <!-- 類別 -->
            <v-layout wrap>
              <v-flex
                xl1
                md2>
                {{ $ln('類別') }}<br>
                <span class="red--text body-2">{{ `(${$ln('必須選擇一種')})` }}</span>
              </v-flex>

              <v-flex
                xl11
                md10>
                <span class="d-inline-block mr-3">
                  <v-checkbox
                    class="v-input-checkbox"
                    v-model="quickSettingPlaySelectAll"
                    :label="$ln('各項設定(全部)')"
                  />
                </span>

                <span
                  class="d-inline-block mr-3"
                  v-for="(item, i) in quickSettingPlay.items"
                  :key="i">
                  <v-checkbox
                    class="v-input-checkbox"
                    v-model="quickSettingPlay.selected"
                    :label="$ln(item.name)"
                    :value="item.id"
                    number
                  />
                </span>
              </v-flex>
            </v-layout>

            <!-- 快速設定金額 -->
            <v-layout wrap>
              <v-flex
                xl1
                md2>
                <div class="pt-2">
                  {{ $ln('快速設定金額') }}
                </div>
              </v-flex>

              <v-flex class="pt-0"
                xl11
                md10>
                <!-- 返水A盤 -->
                <span class="d-inline-block mr-2">
                  <v-layout wrap>
                    <v-flex class="pr-0">
                      <v-form v-model="quickSettingAmount.commission_A.valid" @submit="" onSubmit="return false;">
                        <v-text-field
                          v-model.number="quickSettingAmount.commission_A.amount"
                          @focusout="changeGameSet('commission_A')"
                          :rules="quickSettingAmount.rules"
                          :label="`${$ln('返水A盤')}(${$ln('足球')})`"
                        />
                      </v-form>
                    </v-flex>
                  </v-layout>
                </span>
                &nbsp;&nbsp;&nbsp;
                <!-- 返水B盤 -->
                <span class="d-inline-block mr-2">
                  <v-layout wrap>
                    <v-flex class="pr-0">
                      <v-form v-model="quickSettingAmount.commission_B.valid" @submit="" onSubmit="return false;">
                        <v-text-field
                          v-model.number="quickSettingAmount.commission_B.amount"
                          @focusout="changeGameSet('commission_B')"
                          :rules="quickSettingAmount.rules"
                          :label="`${$ln('返水B盤')}(${$ln('足球')})`"
                        />
                      </v-form>
                    </v-flex>
                  </v-layout>
                </span>
                &nbsp;&nbsp;&nbsp;
                <!-- 返水C盤 -->
                <span class="d-inline-block mr-2">
                  <v-layout wrap>
                    <v-flex class="pr-0">
                      <v-form v-model="quickSettingAmount.commission_C.valid" @submit="" onSubmit="return false;">
                        <v-text-field
                          v-model.number="quickSettingAmount.commission_C.amount"
                          @focusout="changeGameSet('commission_C')"
                          :rules="quickSettingAmount.rules"
                          :label="`${$ln('返水C盤')}(${$ln('足球')})`"
                        />
                      </v-form>
                    </v-flex>
                  </v-layout>
                </span>
                &nbsp;&nbsp;&nbsp;
                <!-- 返水設定 -->
                <span class="d-inline-block mr-2">
                  <v-layout wrap>
                    <v-flex class="pr-0">
                      <v-form v-model="quickSettingAmount.commission.valid" @submit="" onSubmit="return false;">
                        <v-text-field
                          v-model.number="quickSettingAmount.commission.amount"
                          @focusout="changeGameSet('commission')"
                          :rules="quickSettingAmount.rules"
                          :label="$ln('返水設定')"
                        />
                      </v-form>
                    </v-flex>
                  </v-layout>
                </span>
                &nbsp;&nbsp;&nbsp;
                <!-- 單注上限 -->
                <span class="d-inline-block mr-1">
                  <v-layout wrap>
                    <v-flex class="pr-0">
                      <v-form v-model="quickSettingAmount.LCtotal.valid" @submit="" onSubmit="return false;">
                        <v-text-field
                          v-model.number="quickSettingAmount.LCtotal.amount"
                          @focusout="changeGameSet('LCtotal')"
                          :rules="quickSettingAmount.rules"
                          :label="$ln('單注上限(萬)')"
                        />
                      </v-form>
                    </v-flex>
                  </v-layout>
                </span>
                &nbsp;&nbsp;&nbsp;
                <!-- 單場上限 -->
                <span class="d-inline-block mr-1">
                  <v-layout wrap>
                    <v-flex class="pr-0">
                      <v-form v-model="quickSettingAmount.teamLCtotal.valid" @submit="" onSubmit="return false;">
                        <v-text-field
                          v-model.number="quickSettingAmount.teamLCtotal.amount"
                          @focusout="changeGameSet('teamLCtotal')"
                          :rules="quickSettingAmount.rules"
                          :label="$ln('單場上限(萬)')"
                        />
                      </v-form>
                    </v-flex>
