<script>
import { common } from '@/utils'
import moment from 'moment'
import _account from "@/utils/api/account";
import Edit from '@/mixins/account-management/level-management/Edit'

export default {
  name: 'EditMember',
  mixins:[Edit],
  data () {
    return {
      // 層級代號
      level: 1,
      // 上層層級代號
      upLevel: 2,
      // 修改會員資料 @看看能不能寫繼承@
      Items: {
        dialogOpen: false,
        valid: false,
        // 此帳號最後修改時間(修改時間)
        lastDateTime: '',
        // 登入時間
        loginTime: '',
        // 投注時間
        betTime: '',
        // 帳號
        account: '',
        // 名稱資料
        nameItems: {
          // 名稱
          name: '',
          nameRules: [] // *initLanguage*
          // nameRules: [
          //   v => !!v || '不能為空',
          //   v => ((/^[\u4e00-\u9fa5_A-Za-z0-9\(\)\_\-]+$/.test(v)) || v === '') || '不能為特殊符號或空格，除了「括號()」、「底線_」和「-」',
          //   v => v.length <= 20 || '不能超過 20 個字元'
          // ]
        },
        // (體育)信用額度 [信用 / 剩餘額度]
        sportCreditItems: {
          todayDate: moment().add(0, 'days').format('MM-DD'), // 當日日期
          tomorrowDate: moment().add(1, 'days').format('MM-DD'), // 明日日期
          amount: 0,
          suffixLeft: '', // *initLanguage*
          // suffixLeft: '(萬) / ',
          suffixRight: '', // *initLanguage*
          // suffixRight: ' (萬)',
          disabled: true,
          show: true
        },
        // (體育)現金額度 [現金額度]
        sportCashItems: {
          amount: 0,
          suffixLeft: '', // *initLanguage*
          // suffixLeft: '(元) / ',
          suffixRight: '', // *initLanguage*
          // suffixRight: ' (元)',
          disabled: true,
          show: false
        },
        // 「體育」
        // (體育)額度
        sportQuota: 0,
        // (體育)剩餘額度
        sportSurplusQuota: 0,
        // (體育)上限剩餘額度值
        sportUpLimitQuota: 0,
        // 當日額度 (信用站會員帳號才有)
        sportTodayQuota: 0,
        // 明日額度 (信用站會員帳號才有)
        sportTomorrowQuota: 0,
        // 「BingoStar」
        // (BingoStar)額度
        bingoQuota: 0,
        // (BingoStar)剩餘額度
        bingoSurplusQuota: 0,
        // (BingoStar)上限剩餘額度值
        bingoUpLimitQuota: 0,
        // (BingoStar)收回贏額額度值
        bingoRecoverWin: 0,
        // 「Super彩球」
        // (Super彩球)額度
        lotteryQuota: 0,
        // (Super彩球)剩餘額度
        lotterySurplusQuota: 0,
        // (Super彩球)上限剩餘額度值
        lotteryUpLimitQuota: 0,
        // 「皇家」
        // (皇家)額度
        royalQuota: 0,
        // (皇家)剩餘額度
        royalSurplusQuota: 0,
        // (皇家)上限剩餘額度值
        royalUpLimitQuota: 0,
        // (皇家)收回贏額額度值
        royalRecoverWin: 0,
        // (皇家)上層押分限注
        royalUpLimitLevel: '',
        // 單注下限
        downLimitItems: {
          amount: '', // 帶入修改前值
          minLimit: '', // 最小限制值(上層值)
          rules: [], // *initLanguage*
          // rules: [
          //   v => (v !== '') || `不能為空`,
          //   v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || `只能數字不能負數和空格`,
          //   v => (v >= parseFloat(this.Items.downLimitItems.minLimit)) || `要大於等於 ${this.Items.downLimitItems.minLimit}`
          // ],
          // 因為一開始未取到 minLimit 值，所以先預設 disable，不然過濾機制會先過濾
          disabled: true,
          show: true // 會員管理必顯示
        },
        // 是否允許登入
        whetherAllowLogin: {
          selected: '',
          options: [
            {
              text: '是',
              value: 0
            },
            {
              text: '否',
              value: 1
            }
          ]
        },
        // 允許投注
        whetherAllowBet: {
          selected: '',
          options: [
            {
              text: '是',
              value: 0
            },
            {
              text: '否',
              value: 1
            }
          ]
        },
        // 備註
        remark: {
          contents: ''
        }
      },
      // 開放比賽類別
      openSportGameItems: {
        // selected: ['s1', 's2', 's3', 's4', 's5', 's6', 's7', 's8', 's9', 's10', 's11', 's12', 's13', 's14', 's15'],
        selected: [],
        items: [
          { id: 's1', name: '美棒' },
          { id: 's2', name: '日棒' },
          { id: 's3', name: '台棒' },
          { id: 's4', name: '韓棒' },
          { id: 's5', name: '冰球' },
          { id: 's6', name: '籃球' },
          { id: 's7', name: '彩球' },
          { id: 's8', name: '美足' },
          { id: 's9', name: '網球' },
          { id: 's10', name: '足球' },
          { id: 's11', name: '指數' },
          { id: 's12', name: '賽馬＆賽狗' },
          { id: 's13', name: '電競' },
          { id: 's14', name: '其他' },
          { id: 's15', name: '世足' }
        ]
      },
      // 開放[足球]比賽類別-ABC盤(1: A盤, 2: B盤, 3: C盤)
      openSportSoccerABCItems: {
        show: true,
        selected: 1,
        options: [
          { text: 'A盤', value: 1, disabled: false },
          { text: 'B盤', value: 2, disabled: false },
          { text: 'C盤', value: 3, disabled: false }
        ]
      },
      // 開放[世足]比賽類別-ABC盤(1: A盤, 2: B盤, 3: C盤)
      openSportFifaABCItems: {
        show: true,
        selected: 1,
        options: [
          { text: 'A盤', value: 1, disabled: false },
          { text: 'B盤', value: 2, disabled: false },
          { text: 'C盤', value: 3, disabled: false }
        ]
      }
    }
  },
  methods: {
    async init () {
      this.initLanguage()

      this.$toastr.options.timeOut = 3600000 // 一小時後自動消失
      this.$toastr.options.extendedTimeOut = 1000 // 當使用者觸碰到提示訊息時，離開後超過此設定時間則隱藏提示訊息
      this.$toastr.options.closeButton = true // 顯示關閉按鈕

      // 設定當前編輯資料
      if (this.$route.query.account) {
        let params = {
          l_lv:this.level ,
          searchAccount: this.$route.query.account
        }
        let res = await _account.getAccount(params);

        if (res.hasOwnProperty('list')) {
          this.reload = new Date().getTime() ;
          await this.setAccount(res.list)
        }

        // 拆帳百分比**在模組元件設置(會員管理無拆帳百分比)

        // 開放比賽類別
        let getOpenGame = []
        for (let key in this.userData.status) {
          if (this.userData.status[key] === 0) getOpenGame.push('s' + key)
        }
        this.openSportGameItems.selected = getOpenGame
        // 開放[足球]ABC盤
        this.openSportSoccerABCItems.selected = this.userData.status.g_fb
        // 開放[世足]ABC盤(未開放)
        // this.openSportFifaABCItems.selected = this.userData.status.g_fifa

        // 球類設定資訊(載入時判斷是否顯示各球類修改列表)
        for (let key in this.mapSportName) {
          // editGameSetItems 是否有 this.mapSportName[i] 這個 key
          if (this.editGameSetItems.hasOwnProperty(key)) {
            // 隱藏表
            if (this.showGameSetItems.selected.indexOf(key) === -1) this.editGameSetItems[key].show = false
            // 顯示表
            else this.editGameSetItems[key].show = true
          }
        }
        // 載入額度預設值
        this.loadQuota()
        // 載入當日與明日體育額度預設值
        this.loadTodayAndTomorrowQuota()
        // @API優化修改@
        this.loadGameSet()
        // @API優化修改@
        this.loadDefaultGameSet()

        // 若是編輯搜尋帳號，取得編輯帳號的上層帳號
        if (this.$route.query.searchStatus) {
          // 載入狀態(開始)
          this.loadingStatus = true
          this.getDownAccount({ accountLine: this.$route.query.account })
            .then(res => {
              if (res.status === 200) {
                if (res.data.hasOwnProperty('result') && res.data.result.hasOwnProperty('accountLine')) {
                  // 會員上層層級2
                  // console.log('up帳號: (會員上層層級2)', res.data.result.accountLine['2'])
                  this.searchUpAccount = res.data.result.accountLine['2']

                  /* 刷新各遊戲上層資訊 */
                  this.loadUpAccountAllInfo()
                }
              }
              // 載入狀態(結束)
              this.loadingStatus = false
            })
            .catch(err => {
              // 載入狀態(結束)
              this.loadingStatus = false
              console.error(err)
            })
        } else {
          /* 刷新各遊戲上層資訊 */
          this.loadUpAccountAllInfo()
        }
      } else {
        // 無物件資料，則轉跳上一頁
        this.toBack()
      }
      this.updateDashboardInfo()
      // 直接不開放世足
      this.closeGameType('s15')
    },
    toBack () {
      if (parseInt(sessionStorage.getItem('super.manage.level')) === 2) {
        this.$router.push({
          path: '/manage/account-management/member-management/directList',
          query: {
            pageType: 'edit'
          }
        })
      } else {
        if (this.$route.query.searchStatus && this.searchUpAccount) {
          this.$router.push({
            path: '/manage/account-management/member-management',
            query: {
              upAccount: this.searchUpAccount,
              pageType: 'edit'
            }
          })
        } else if (this.$route.query.upAccount) {
          this.$router.push({
            path: '/manage/account-management/member-management',
            query: {
              upAccount: this.$route.query.upAccount,
              pageType: 'edit'
            }
          })
        } else {
          this.$router.push({
            path: '/manage/account-management/member-management',
            query: {
              pageType: 'edit'
            }
          })
        }
      }
    },
    // 載入當日與明日體育額度預設值(只有會員管理有)
    loadTodayAndTomorrowQuota () {
      try {
        this.getMemberTodayAndTomorrowQuota({ balltype: 1, l_no: this.Items.account })
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              if (res.response.status === 401) return
              // 錯誤訊息
              if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
              else this.$toastr.error(this.$ln(`取得當日與明日體彩額度值失敗`))
              console.error(res.response)
            } else if (res.status === 200) { // success
              // http 200 回應錯誤訊息
              if (res.data.hasOwnProperty('errors')) {
                console.error('response 200 error: ', res.data)
              } else if (res.data.hasOwnProperty('result')) {
                // console.log(`取得 ${this.Items.account} 當日與明日體彩額度值成功`, res.data.result)
                if (res.data.result.hasOwnProperty('today_quota')) {
                  // 設置(體育)當日額度值
                  this.Items.sportTodayQuota = res.data.result.today_quota
                }
                if (res.data.result.hasOwnProperty('tomorrow_quota')) {
                  // 設置(體育)明日額度值
                  this.Items.sportTomorrowQuota = res.data.result.tomorrow_quota
                }
              }
            }
          })
          .catch(err => {
            console.error(err)
          })
      } catch (error) {
        this.$toastr.error(this.$ln(`站台資料錯誤，請聯絡客服`))
        console.error(error)
      }
    },
    // 取得球類設定與佔成設定上下限制值(依球類或遊戲種類代號取得) @看看能不能寫繼承@
    loadDefaultGameSet (type) {
      // balltype 於 GET API 改參數值
      if (type === 16) {
        type = 'bingo'
        // @201909移除賓果@
        return
      }
      if (type === 17) type = 'lottery'
      if (type === 18) type = 'royal'
      // 取不到帳號 return
      if (!this.Items.account) return
      try {
        // 修改會員，送 level 為假資料使用
        this.getOccupyAndGameSetLimit({ account: this.Items.account, balltype: type, level: 1 })
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              if (res.response.status === 401) return
              // 錯誤訊息
              if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
              else this.$toastr.error(this.$ln(`取得上層帳號球類預設錯誤`))
              console.error(res.response)
            } else if (res.status === 200) { // success
              // http 200 回應錯誤訊息
              if (res.data.hasOwnProperty('errors')) {
                console.error('response 200 error: ', res.data)
              } else if (res.data.hasOwnProperty('result')) {
                // console.log(`取得上層帳號球類預設成功`)
              }
            }
          })
          .catch(err => {
            console.error(err)
          })
      } catch (error) {
        this.$toastr.error(this.$ln(`站台資料錯誤，請聯絡客服`))
        console.error(error)
      }
    },
    // 取得當前上層帳號值
    getUpAccountValue () {
      // 編輯會員，若登入代理帳號，則送的上層帳號為「登入帳號」，否則送「取得的上層帳號」
      if (parseInt(sessionStorage.getItem('super.manage.level')) === 2) {
        return sessionStorage.getItem('super.manage.account')
      } else {
        if (this.$route.query.searchStatus && this.searchUpAccount) {
          return this.searchUpAccount
        } else if (this.$route.query.upAccount) {
          return this.$route.query.upAccount
        }
      }
    },
    /**
     * 編輯管端帳號 @看看能不能寫繼承@
     * @params type        球類種類
     * @params showToastr  是否顯示 toastr
     **/
    editSuperAccount (type, showToastr) {
      let editData = {
        l_no: this.Items.account,
        l_name: this.Items.nameItems.name,
        remark: this.Items.remark.contents,
        balltype: type
      }
      // 單注下限
      if (this.Items.downLimitItems.show) {
        editData.down_limit = this.Items.downLimitItems.amount
      }
      // 允許登入
      if (this.upStatus.sport === 0) editData.l_status = this.Items.whetherAllowLogin.selected
      // 允許投注
      if (this.upClose.sport === 0) editData.l_close = this.Items.whetherAllowBet.selected
      // 球類設定資訊
      if (!common.isEmpty(this.editGameSetItems['s' + type].editItems)) {
        editData = $.extend(editData, this.editGameSetItems['s' + type].editItems)
      }

      // 開放比賽類別
      for (let i in this.openSportGameItems.items) {
        if (this.openSportGameItems.selected.indexOf(this.openSportGameItems.items[i].id) !== -1) {
          // 開放送 0
          editData[`g_open${this.openSportGameItems.items[i].id.split('s')[1]}`] = 0
        } else {
          // 未開放送 1
          editData[`g_open${this.openSportGameItems.items[i].id.split('s')[1]}`] = 1
        }
      }
      // 開放[足球]ABC盤
      if (this.openSportSoccerABCItems.show) editData.g_fb = this.openSportSoccerABCItems.selected
      // 開放[世足]ABC盤(未開放)
      // if (this.openSportFifaABCItems.show) editData.g_fifa = this.openSportSoccerABCItems.selected

      // console.info(`編輯管端帳號(${type}): `, editData)
      try {
        this.putSuperAccount(editData)
          .then(res => {
            setTimeout(() => {
              if (Object.prototype.toString.call(res) === '[object Error]' &&
                  res.hasOwnProperty('response') &&
                  res.response.status !== 200) {
                // 錯誤訊息
                if (res.response.data.hasOwnProperty('errors')) {
                  common.errorMsgHandler(res.response.data.errors)
                } else {
                  // this.$toastr.error(`${this.$ln(this.mapSportName['s' + type])} ${this.$ln('修改失敗')}`)
                  this.showError(`${this.$ln(this.mapSportName['s' + type])} ${this.$ln('修改失敗')}`)
                }
              } else if (res.status === 200) { // success
                // if (showToastr) this.$toastr.success(this.$ln(`修改成功`), '', {timeOut: 2000, closeButton: false})
                if (showToastr) {
                  this.reload = new Date().getTime()
                  // 清除快速設定
                  this.resetQuickSetting()
                  this.showError(this.$ln(`修改成功`))
                }
                // 修改成功回上一頁
                // this.toBack()
                this.init()
              }

              // 修改中狀態(結束)
              this.modifyingStatus = false
            }, 1000)
          })
          .catch(err => {
            setTimeout(() => {
              if (showToastr) {
                // this.$toastr.error(`${this.$ln(this.mapSportName['s' + type])} ${this.$ln('修改失敗')}`)
                this.showError(`${this.$ln(this.mapSportName['s' + type])} ${this.$ln('修改失敗')}`)
              }

              // 修改中狀態(結束)
              this.modifyingStatus = false
            }, 1000)
          })
      } catch (error) {
        setTimeout(() => {
          // if (showToastr) this.$toastr.error(this.$ln(`站台資料錯誤，請聯絡客服`))
          if (showToastr) {
            this.showError(this.$ln(`站台資料錯誤，請聯絡客服`))
          }

          // 修改中狀態(結束)
          this.modifyingStatus = false
        }, 1000)
      }
    },
    // 不開放遊戲 @看看能不能寫繼承@
    closeGameType (type) {
      switch (type) {
        // 關閉世足
        case 's15':
          // 不顯示[世足]開放比賽類別 items
          let newOpenSportGameItems = []
          for (let i in this.openSportGameItems.items) {
            if (this.openSportGameItems.items[i].id !== 's15') newOpenSportGameItems.push(this.openSportGameItems.items[i])
          }
          this.openSportGameItems.items = newOpenSportGameItems
          // 不顯示[世足]開放比賽類別 selected
          let openSportGameSelectedItems = []
          for (let i in this.openSportGameItems.selected) {
            if (this.openSportGameItems.selected[i] !== 's15') openSportGameSelectedItems.push(this.openSportGameItems.selected[i])
          }
          this.openSportGameItems.selected = openSportGameSelectedItems

          // 不顯示[世足]ABC盤
          this.openSportFifaABCItems.show = false

          // 不顯示[世足]修改類別 items
          let newShowGameSetItems = []
          for (let i in this.showGameSetItems.items) {
            if (this.showGameSetItems.items[i].id !== 's15') newShowGameSetItems.push(this.showGameSetItems.items[i])
          }
          this.showGameSetItems.items = newShowGameSetItems
          // 不顯示[世足]修改類別 selected
          let showGameSetSelectedItems = []
          for (let i in this.showGameSetItems.selected) {
            if (this.showGameSetItems.selected[i] !== 's15') showGameSetSelectedItems.push(this.showGameSetItems.selected[i])
          }
          this.showGameSetItems.selected = showGameSetSelectedItems
          break
        // 關閉BingoStar
        case 's16':
          this.dashboardOpenStatus.bingo = false
          break
        // 關閉Super彩球
        case 's17':
          this.dashboardOpenStatus.lottery = false
          break
        // 關閉皇家視訊
        case 's18':
          this.dashboardOpenStatus.royal = false
          break
      }
    }
  }
}
</script>

